<template>
	<view class="container">
		<view class="top">
			<view class="tpl-icon">
				<uni-icons type="contact-filled" size="34" color="#d6d6d6" style="float: left;"></uni-icons>
				<p>admin</p>
			</view>
			<view class="tpr" @tap="loginout">
				<p>退出</p>
			</view>
		</view>
		<!-- 内容 -->
		<view class="middle">
			<view class="shops" @tap="ShopsList">
				<unicloud-db v-slot:default="{data, loading, error, options}" collection="shops">
					<view v-if="error">{{error.message}}</view>
					<view v-else>
						<view class="table">
							<p>档口</p>
							<span>更多></span>
							<uni-list>
								<uni-list-item v-for="item in data" :key="item._id" :title="item.s_name"></uni-list-item>
							</uni-list>
						</view>
					</view>
				</unicloud-db>
			</view>
			<view class="coupons" @tap="couponsList">
				<unicloud-db v-slot:default="{data, loading, error, options}" collection="coupons">
					<view v-if="error">{{error.message}}</view>
					<view v-else>
						<view class="table">
							<p>优惠券</p>
							<span>更多></span>
							<uni-list>
								<uni-list-item v-for="item in data" :key="item._id" :title="item.c_name"></uni-list-item>
							</uni-list>
						</view>
					</view>
				</unicloud-db>
			</view>
			<view class="user_coupon" @tap="user_couponList">
				<unicloud-db v-slot:default="{data, loading, error, options}" collection="user_coupon">
					<view v-if="error">{{error.message}}</view>
					<view v-else>
						<view class="table">
							<p>用户所拥有优惠券</p>
							<span>更多></span>
							<uni-list>
								<uni-list-item v-for="item in data" :key="item._id" :title="item._id"></uni-list-item>
							</uni-list>
						</view>
					</view>
				</unicloud-db>
			</view>
			<view class="food" @tap="foodList">
				<view class="table">
					<p>食物点评</p>
					<span>更多></span>
					<uni-list>
						<uni-list-item v-for="item in food" :key="item._id" :title="item.f_name"></uni-list-item>
					</uni-list>
				</view>
			</view>
			<view class="foodEvalute" @tap="foodEvaluateList">
				<view class="table">
					<p>食物点评</p>
					<span>更多></span>
					<uni-list>
						<uni-list-item v-for="item in foodEvaluate" :key="item._id" :title="item.content"></uni-list-item>
					</uni-list>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				food: [],
				foodEvaluate: []
			}
		},
		methods: {
			ShopsList() {
				uni.navigateTo({
					url: '/pages_admin/shops/list'
				})
			},
			couponsList() {
				uni.navigateTo({
					url: '/pages_admin/coupons/list'
				})
			},
			user_couponList() {
				uni.navigateTo({
					url: '/pages_admin/user_coupon/list'
				})
			},
			foodList() {
				uni.navigateTo({
					url: '/pages_admin/food/list'
				})
			},
			InfoShow() {
				const db = uniCloud.database();
				db.collection('food-evaluate').orderBy('create_date desc').limit(5).get().then(res => {
					console.log(res.result.data)
					this.foodEvaluate = res.result.data
				}).catch(err => {
					console.log(err)
				})
				db.collection('food').orderBy('create_date desc').limit(5).get().then(res => {
					console.log(res.result.data)
					this.food = res.result.data
				}).catch(err => {
					console.log(err)
				})
			},
			foodEvaluateList() {
				uni.navigateTo({
					url: '/pages_admin/food-evaluate/list'
				})
			},
			loginout() {
				uni.redirectTo({
					url:'/pages/login/login'
				})
			}
		},
		onLoad() {
			this.InfoShow()
			// this.getuser()
		},
		components: {

		}
	};
</script>

<style lang="scss" scoped>
	.search {
		margin: 20rpx;
	}

	.top {
		// border: 1px solid blue;
		width: 100%;
		height: 45px;
	}

	.tpl-icon {
		// border: 1px solid blue;
		float: left;
		width: 50%;
	}

	.tpl-icon p {
		// border: 1px solid red;
		margin: 5px 95px 0 0;
		float: right;
	}

	.tpr {
		// border: 1px solid blue;
		float: right;
		margin: 10px 20px 0 0;
	}

	.table {
		border: 0.5px solid gray;
		margin-bottom: 20px;
	}

	.table p {
		// border: 1px solid blue;
		display: inline;
		margin-left: 10px;
	}

	.table span {
		// border: 1px solid blue;
		float: right;
		margin-right: 10px;
	}
</style>
